<template>
<div>
        <el-card class="box-card">
             <el-alert
                title=""
                type="info"
                center
                show-icon>
            </el-alert>
            <el-steps :active="activeIndex - 0" finish-status="success" :space="200" align-center>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
            </el-steps>

            <el-form :model="addForm" :rules="addFormrules" ref="addFormRef" label-width="100px" label-position='top'>
                <el-tabs class="tabs" v-model="activeIndex" :tab-position="'left'"
                :before-leave='beforeTab'>
                    <el-tab-pane label="" name='0'>
                        <el-form-item label="" prop="name">
                            <el-input v-model="addForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="" type='number' prop="price">
                            <el-input v-model="addForm.price"></el-input>
                        </el-form-item>
                        <el-form-item label="" type='number' prop="weight">
                            <el-input v-model="addForm.weight"></el-input>
                        </el-form-item>
                        <el-form-item label="" type='number' prop="num">
                            <el-input v-model="addForm.num"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="fl">
                            <el-cascader
                            v-model="addForm.fl"
                            :options="shopList"
                            expand-trigger='hover'
                            :props="fllist"
                            @change="handleChange"></el-cascader>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name='1'></el-tab-pane>
                    <el-tab-pane label="" name='2'></el-tab-pane>
                    <el-tab-pane label="" name='3'></el-tab-pane>
                    <el-tab-pane label="" name='4'></el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
</div>
</template>
<script>
exportdefault{
data(){
return{
            activeIndex:0,
            addForm:{
                name:'',
                price:0,
                weight:0,
                num:0,
                fl:[],
            },
            addFormrules:{
                name: [
                    { required: true, message: '', trigger: ['blur','change'] },
                ],
                price: [
                    { required: true, message: '', trigger: ['blur','change'] },
                ],
                weight: [
                    { required: true, message: '', trigger: ['blur','change'] },
                ],
                num: [
                    { required: true, message: '', trigger: ['blur','change'] },
                ],
                fl: [
                    { required: true, message: '', trigger: ['blur','change'] },
                ],
            },
            shopList:[],
            fllist:{
                children:'children',
                value:'cat_id',
                label:'cat_name',
            }
};
},
created(){
        
},
mounted(){
        this.list()
},
methods:{
        async list(){
            this.shopList
            let { data } = await this.$http.get('categories')
            console.log(data)
            this.shopList=data.data
        },
        handleChange(){
            if(this.addForm.fl.length !== 3){
                this.addForm.fl=[]
                this.$message({
                    type: "warning",
                    duration: 2000,
                    message: "!",
                });
            }
        },
        beforeTab(activeName,oldActiveName){
            // console.log(''+activeName)
            // console.log(''+oldActiveName)
            if(oldActiveName === '0' && this.addForm.fl.length !== 3){
                this.$message({
                    type: "error",
                    duration: 2000,
                    message: "!",
                });
                return false
            }
        }
}
};
</script>

<stylescoped lang='scss'>
.box-card {
  width: 100%;
  line-height: 33px;
  margin-top: 20px;
  text-align: left;
}
.el-table {
  margin-top: 20px;
}
.el-alert{
    line-height: 30px;
}
.el-steps{
    margin-top: 20px;
}
.el-step__title .is-process{
    font-size: 13px;
}
.tabs{
    margin-top: 30px;
}
</style>
